
.vertical-navbar {
	height: 100vh;
	min-width: 250px;
	max-width: 250px;

	color: #fafafa;

	background-color: #212843;
}
	
	/* Le logo */
	.vertical-navbar img {
		width: 50%;

		margin-left: 5%;
		margin-right: 45%;
		margin-top: 15px;
		margin-bottom: 25px;
	}

	/* Les sections */
	.vertical-navbar__nav-section {
		margin-left: 10px;

		padding-bottom: 20px;
	}

		.nav-section__big-title {
			font-size: 1.2em;

			padding-bottom: 7px;
		}

		.nav-section__small-titles {
			margin: 0px;
			margin-left: 15px;
		}

			.nav-section__small-titles p {
				margin: 0px;
				padding-bottom: 5px;
			}