
.horizontal-navbar {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

	height: 65px;
	width: 100%;

	box-shadow: 0px 0px 10px #aaaaaa;

	background-color: #ffffff;
}

	.horizontal-navbar__left-part {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;

		height: 100%;
		width: 50%;
	}

		.left-part__menu-icon {
			height: 25px;

			margin-left: 20px;
		}

	.horizontal-navbar__right-part {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;

		height: 100%;
		width: 50%;
	}

		.right-part__notif-icon {
			height: 35px;
		}

		.right-part__profil-icon {
			height: 40px;

			margin-right: 15px;
			margin-left: 20px;
		}